import React, { Component } from 'react';

export default class Header extends Component {
    state = {
        search: ""
    }

    onChangeText(ev){
        this.setState({ search: ev.target.value })
    }

    render(){
        return (
            <div className="Header">
                <div className="container-big flex horizontal">
                    <div className="logo-container flex-1 flex flex-center">
                        <img src={`${process.env.PUBLIC_URL}/image/logo.png`} className="logo" width="90%" />
                    </div>
                    <div className="flex-2 flex flex-center">
                        <input
                            value={this.state.search}
                            placeholder={"Pesquise aqui..."}
                            className="form-input search"
                            name="search" />
                        <button className="btn btn-search">
                            <i className="fa fa-search"></i>
                        </button>
                    </div>
                    <div className="flex-1 flex horizontal-only flex-center">
                        <div className="minha-conta flex-1 flex horizontal-only flex-right">
                            <i className="fa fa-user fa-lg"></i>
                            <span>Minha Conta</span>
                        </div>
                        <div className="flex-1 flex horizontal-only flex-center">
                            <img src={`${process.env.PUBLIC_URL}/image/cart-icon.png`} width="30px" />
                            <span className="icon-cart-number">&nbsp;0</span>
                            {/* <span>Carrinho</span> */}
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}